@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.app__main {
  display: -webkit-flex;
  display: flex;
  background-color: black;
}

.app__login {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
  background-color: rgb(46, 42, 42);
}

.app__login>button{
  border: none;
  font-size: 24px;    
  background-color: rgb(5, 71, 3);
  color: white;
  padding: 10px 20px;
  border-radius: 6px;
  transition: all 0.2s;
}

.app__login>button:hover{
  cursor: pointer;
  background-color: rgb(133, 14, 14);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


.header {
    display: -webkit-flex;
    display: flex;
    height: 60px;
    border-bottom: 1px solid rgb(223, 219, 245);
    width: 100vw;
    background-color: rgb(46, 42, 42);
}

.header>div {
    padding: 12px;
}

.header__logo {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

.header__logo>img {
    height: 100%;
    object-fit: contain;
}

.header__logo>span {
    color: burlywood;
    font-size: 20px;
    font-weight: 500;
    margin-left: 16px;
}

.header__searchContainer {
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px;
    color: black;
}

.header__searchBar {
    width: 45%;
    height: 120%;
    border-radius: 6px;
    background-color: burlywood;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 8px;
    }

.MuiSvgIcon-root {
    color: rgb(19, 16, 16);
}

.header__searchBar>input {
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 60%;
    font-size: 16px;
    color: rgb(7, 6, 6);
    background: none;
    border: none;
    margin-left: 12px;
}

.header__searchBar>input:focus {
    outline: none;
    color: black;
}
