.sidebar{
    width: 15%;
    height: 100vh;
    margin-right: 5px;
    background-color: rgb(46, 42, 42);
}

hr{
    background-color: rgb(197, 197, 197);
    height: 1px;
    border: none;
}
.newFile {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 12px 0;
    padding-left: 20px;
    -webkit-flex:1 1;
            flex:1 1;
    
}

.newFile__container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    /* TRouBLe */
    padding: 6px 32px 6px 8px;
    border-radius: 50px;
    box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302), 0 1px 3px 1px rgba(60, 64, 67, 0.149);
    cursor: pointer;
    background-color: burlywood;
}

.newFile__container>p{
    margin-left: 14px;
}

.Chat__message>input{
    color:rgb(46, 42, 42);
    width: 8%;
    font-size: 10px;
    height: 25px;
    background-color: burlywood;
    border-radius: 10%;
    margin-left: 5px;
    position: fixed;
    bottom: 2.5%;


}
.Chat__message>button{
    width: 3%;
    height: 25px;
    font-size: 10px;
    color:burlywood;
    background-color: rgb(46, 42, 42);
    border-radius: 25%;
    border-color: burlywood;
    margin-left: 2%;
