}

.message{
    -webkit-flex:1 1;
            flex:1 1;
    color:burlywood;
    margin-left:10px;
}
.fileView{
    width: 100%;
    background-color: black;
}

.fileView__row{
    height: 250px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.fileView__titles{
    display: -webkit-flex;
    display: flex;
    margin-bottom: 5px;
    color: rgb(231, 209, 209);
}

.fileView__titles>div>*{
    margin: 5px;
}

.fileView__titles--left{
    -webkit-flex: 1 1;
            flex: 1 1;
}

.fileView__titles--right{
    display: -webkit-flex;
    display: flex;
}
.fileItem{
    height: 55px;
    border-bottom: 1px solid rgb(219, 219, 219);
    border-top: 1px solid rgb(219, 219, 219);
    width: 100%;
    background-color: rgb(46, 42, 42);
}

.fileItem>a{
    height: 100%;
    display: -webkit-flex;
    display: flex;
    text-decoration: none;
    color: rgb(240, 211, 211);
}

.fileItem>a>div{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.fileItem>a>div>*{
    margin: 10px;
}

.fileItem--left{
    -webkit-flex: 1 1;
            flex: 1 1;
}
.fileCard {
    height: 190px;
    width: 240px;
    border-radius: 10px;
    border: 1px solid rgb(219, 219, 219);
    margin: 5px;
    background-color: rgb(46, 42, 42);
    color: blanchedalmond;
}

.fileCard--top {
    height: 70%;
    border-bottom: 1px solid rgb(219, 219, 219);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.filecard .MuiSvgIcon-root{
    color: burlywood;
}
.fileCard--bottom {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    height: 30%;
}

.fileCard--bottom>p {
    width: 90%;
    overflow: hidden;
    white-space: nowrap;
}
.sideIcons{
    width: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    border-left: 1px solid rgb(219, 219, 219);
    background-color: rgb(46, 42, 42);
}

.sideIcons__top{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.sideIcons__top>img{
    object-fit: contain;
    width: 30px;
    margin: 10px 0;
}

.sideIcons>hr{
    margin: 12px 0;
    width: 90%;
}

.sideIcons__plusIcon{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
