.header {
    display: flex;
    height: 60px;
    border-bottom: 1px solid rgb(223, 219, 245);
    width: 100vw;
    background-color: rgb(46, 42, 42);
}

.header>div {
    padding: 12px;
}

.header__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.header__logo>img {
    height: 100%;
    object-fit: contain;
}

.header__logo>span {
    color: burlywood;
    font-size: 20px;
    font-weight: 500;
    margin-left: 16px;
}

.header__searchContainer {
    flex: 1;
    display: flex;
    align-items: center;
    padding: 8px;
    color: black;
}

.header__searchBar {
    width: 45%;
    height: 120%;
    border-radius: 6px;
    background-color: burlywood;
    display: flex;
    align-items: center;
    padding: 0 8px;
    }

.MuiSvgIcon-root {
    color: rgb(19, 16, 16);
}

.header__searchBar>input {
    flex: 1;
    height: 60%;
    font-size: 16px;
    color: rgb(7, 6, 6);
    background: none;
    border: none;
    margin-left: 12px;
}

.header__searchBar>input:focus {
    outline: none;
    color: black;
}
